import React, { Component } from 'react';
import { Provider, Heading, Subhead, Text, Flex, Relative, Absolute,NavLink, Input, Button, Small,} from 'rebass';

const TITLES = [
    'O teu tempo livre vale dinheiro.',
    'Recebe Recompensas pela tua opinião',
    'Simples mas significante',
    
];

const BODIES = [
    'No carro, no metro ou na fila do talho ajuda as tuas marcas favoritas a criar os produtos que tu realmente queres. E ainda recebes discontos nos teus produtos favoritos!',
    'Lorem Ipsum',
    'Lorem Ipsum 2'
]

class InfoText extends Component{
    state = {
        index : 0,
        fadeIn : true,
    };

    componentDidMount(){
        this.timeout = setTimeout(() => this.setState({ fadeIn: false }), 15000);

        this.animateTitles();
    }

    componentWillUnmount(){
        clearInterval(this.titleInterval);
        clearTimeout(this.timeout);
    }

    animateTitles = () => {
        this.titleInterval = setInterval(() => {
            const index = (this.state.index + 1) % TITLES.length;

            this.setState({index,fadeIn: true});

            this.timeout = setTimeout(() => this.setState({ fadeIn: false }), 15000);
        },20000);
    }

    render(){
        const { fadeIn, index } = this.state;

        const title = TITLES[this.state.index];
        
        const body = BODIES[this.state.index]

        return(
            <div className={fadeIn ? 'title-fade-in' : 'title-fade-out'}>
                {/* <p className={fadeIn ? 'title-fade-in' : 'title-fade-out'}> I am {title}</p> */}
                <Heading  textAlign='left' fontSize={[52]}>{title}</Heading>
                    <Text color='grey' textAlign='left' fontSize={[1, 2]} mt='4'>
                       {body}
                    </Text>
                    <Text color='grey' textAlign='left' fontSize={[1, 2]}>
                      
                    </Text>
            </div>
            
        )
    }
}

export default InfoText;


