import React, { Component } from 'react';
import { Modal, Heading,Subhead, Fixed, Text, Flex, Relative, Absolute,NavLink, Input, Button, Small,Close} from 'rebass'
import axios from 'axios';

class UsersContact extends React.Component {
    state = {
        open:false,
        email: '',
        sent: false,
        buttonText: 'Obter Convite'

    };

    handleOpen = () => {
        this.setState({ open: true});
    };

    handleClose = () => {
        this.setState({ open: false});
    };

    resetForm = () => {
        this.setState({
            name: '',
            message: '',
            email: '',
            buttonText: 'Message Sent'
        })
    };


    formSubmit = (e) => {
        e.preventDefault()
    
        this.setState({
            buttonText: '...sending'
        })
    
        let data = {
            name: this.state.name,
            email: this.state.email,
            message: this.state.message
        }
        
        axios.post('API_URI', data)
        .then( res => {
            this.setState({ sent: true }, this.resetForm())
        })
        .catch( () => {
        console.log('Message not sent')
        })
    }
  

    render(){
        const { classes } = this.props;

        return(
            <div>
                <Flex flexDirection='row' mt={6} mb={5} justifyContent='left' flexWrap='wrap'>
                    <Input placeholder='    Acompanha-nos. Sê o primeiro a usufruir!'  borderColor='black' boxShadow='0 0 16px rgba(0, 0, 0, .25)'
                        onChange={(e) => this.setState({ email: e.target.value})} value={this.state.email} />
                    <Button  mt='3' boxShadow='3' color='white' bg='#46da84' fontSize='2' onSubmit={this.formSubmit}>{this.state.buttonText}</Button>
                </Flex>
            </div>
        );
    }
}

export default UsersContact;